:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.barrier-configurator .bc__options {
  display: flex;
  gap: 32px;
  margin-left: 52px;
  margin-right: 52px;
}

.barrier-configurator .bc__option {
  background-color: var(--color-white);
  display: flex;
  padding-top: 20px;
  padding-left: 38px;
  padding-bottom: 32px;
  padding-right: 38px;
  border-radius: 40px;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  max-width: 50%;
  transition: border-color 0.3s;
}

.barrier-configurator .bc__option:has(.bc__radio-input:checked) {
  box-shadow: 0 0 0 1px var(--text-black);
}

.barrier-configurator .bc__option .bc__option-label {
  width: 100%;
  display: block;
  cursor: pointer;
}

.barrier-configurator .bc__option-content {
  align-self: flex-end;
  width: 100%;
  transition: all 0.3s;
}

.barrier-configurator .bc__option-image {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 300px;
  border-radius: 25px;
  overflow: hidden;
  transition: all 0.3s;
  position: relative;
}

.barrier-configurator .bc__option-image img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.barrier-configurator .bc__option-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 32px;
  margin-right: 24px;
}

.barrier-configurator .bc__option.bc__option-image--small {
  padding-top: 0;
}

.barrier-configurator .bc__option.bc__option-image--small .bc__option-image {
  height: 200px;
}

.barrier-configurator .bc__option.bc__option-image--small .bc__option-wrapper {
  margin-top: 0;
}

.barrier-configurator .bc__option-title {
  display: flex;
  align-items: center;
  gap: 16px;
}

.barrier-configurator .bc__option-title .bc__radio-item {
  flex-shrink: 0;
}

.barrier-configurator .bc__option-title h6 {
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  word-break: break-word;
}

.barrier-configurator .bc__option-list {
  margin-top: 12px;
  margin-left: 48px;
}

.barrier-configurator .bc__option-list li {
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  word-break: break-word;
}

@media screen and (max-width: 1600px) {
  .barrier-configurator .bc__options {
    margin-left: 0;
    margin-right: 0;
  }
  .barrier-configurator .bc__option {
    padding: 20px 40px 32px 36px;
  }
  .barrier-configurator .bc__option-image {
    margin-top: 0;
  }
  .barrier-configurator .bc__option-image img {
    padding: 24px 28px 0 24px;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small {
    padding-top: 0;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-image {
    height: 200px;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-image img {
    padding: 0;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-wrapper {
    margin-top: 0;
  }
  .barrier-configurator .bc__option-title h3 {
    font-size: 24px;
    line-height: 32px;
  }
}

@media screen and (max-width: 1400px) {
  .barrier-configurator .bc__option {
    padding-top: 0;
  }
  .barrier-configurator .bc__option-wrapper {
    margin-top: 16px;
  }
}

@media screen and (max-width: 1200px) {
  .barrier-configurator .bc__option {
    padding-top: 0;
    padding-bottom: 32px;
  }
  .barrier-configurator .bc__options .bc__option {
    border-radius: 31px;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 0;
    padding-bottom: 32px;
  }
  .barrier-configurator .bc__option-wrapper {
    margin-top: 0;
  }
  .barrier-configurator .bc__option-title {
    gap: 8px;
  }
  .barrier-configurator .bc__option-list {
    margin-left: 40px;
    margin-top: 8px;
  }
  .barrier-configurator .bc__option-image {
    margin-top: 0;
  }
  .barrier-configurator .bc__option-image img {
    padding: 32px 32px 0 32px;
  }
}

@media screen and (max-width: 900px) {
  .barrier-configurator .bc__options {
    flex-direction: column;
    gap: 20px;
  }
  .barrier-configurator .bc__option {
    max-width: 100%;
  }
  .barrier-configurator .bc__option-title {
    gap: 8px;
  }
  .barrier-configurator .bc__option-list {
    margin-left: 40px;
    margin-top: 8px;
  }
  .barrier-configurator .bc__option-image img {
    padding: 0 2rem;
  }
  .barrier-configurator .bc__options .bc__option {
    min-height: auto;
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .barrier-configurator .bc__option-list li {
    font-size: 16px;
    line-height: 24px;
  }
  .barrier-configurator .bc__option-title h3 {
    font-size: 24px;
    line-height: 32px;
  }
}

@media screen and (max-width: 712px) {
  .barrier-configurator .bc__options {
    gap: 20px;
  }
  .barrier-configurator .bc__options .bc__option {
    padding-top: 16px;
  }
}

@media screen and (max-width: 600px) {
  .barrier-configurator .bc__options .bc__option {
    border-radius: 25px;
  }
  .barrier-configurator .bc__option-wrapper {
    margin-top: 0;
  }
  .barrier-configurator .bc__option-image {
    height: 200px;
  }
  .barrier-configurator .bc__option-title h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

@media screen and (max-width: 450px) {
  .barrier-configurator .bc__option {
    padding-top: 16px;
    padding-bottom: 24px;
  }
  .barrier-configurator .bc__options .bc__option {
    border-radius: 20px;
    min-height: auto;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 16px;
    padding-bottom: 24px;
  }
  .barrier-configurator .bc__option-title {
    gap: 4px;
  }
  .barrier-configurator .bc__option-image {
    height: 155px;
    margin-top: 0;
    border-radius: 20px;
  }
  .barrier-configurator .bc__option-image img {
    padding: 0;
  }
  .barrier-configurator .bc__options {
    display: flex;
    gap: 12px;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-image {
    height: 100px;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small {
    padding-left: 0;
    padding-right: 0;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-image {
    left: -15px;
  }
  .barrier-configurator .bc__options .bc__option.bc__option-image--small .bc__option-wrapper {
    margin-left: 38px;
  }
  .barrier-configurator .bc__option-wrapper {
    margin-right: 0;
    margin-top: 0;
  }
  .barrier-configurator .bc__option-list {
    margin-left: 0;
    margin-top: 8px;
  }
  .barrier-configurator .bc__option-title h2 {
    font-size: 18px;
    line-height: 24px;
  }
  .barrier-configurator .bc__option-title h3 {
    font-size: 14px;
    line-height: 24px;
  }
  .barrier-configurator .bc__option-wrapper .bc__option-list li {
    font-size: 14px;
    line-height: 20px;
  }
}
